/* global console, document, Excel, Office */
Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("btn-gennerate").onclick = run;
  }
});

function run() {
  return Excel.run(function (context) {
    /**
     * Insert your Excel code here
     */
    const range = context.workbook.getSelectedRange();

    // Read the range address
    range.load("address");

    // Update the fill color
    range.format.fill.color = "yellow";

    return context.sync().then(function () {
      document.getElementById("response-content").textContent = `[Long Test]The range address was ${range.address}.`;
      console.log(`The range address was ${range.address}.`);
    });
  });
}

// This function is called when the "Generate" button is clicked

/** Default helper for invoking an action and handling errors. */
function tryCatch(callback) {
  Promise.resolve()
    .then(callback)
    .catch(function (error) {
      // Note: In a production add-in, you'd want to notify the user through your add-in's UI.
      console.error(error);
    });
}
